import React, { useEffect, useState } from 'react';

const Slide = ({ title, isCurrent }) => {
  const slideClass = isCurrent ? 'slide slide--current' : 'slide';

  return (
    <div className={slideClass}>
      <span className="slide-title">{title}</span>
    </div>
  );
};

const Slideshow = ({ slides }) => {
  const [current, setCurrent] = useState(0);

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrent((prev) => (prev < slides.length - 1 ? prev + 1 : 0));
    }, 2500);

    return () => clearInterval(interval);
  }, [slides.length]);

  return (
    <div className="text-slider">
      <div className="fade">
        {slides.map((slide, index) => (
          <Slide key={index} title={slide} isCurrent={index === current} />
        ))}
      </div>
    </div>
  );
};

const FullscreenVideo = () => {
  const slides = ['Brand', 'Vision', 'Voice', 'Identity', 'Marketing'];

  return (
    <div className="fullscreen-video">
      <video loop autoPlay muted playsInline preload="auto" poster="https://bosscreative.com/wp-content/themes/parada/videos/home/boss_home_video.jpg">
        <source src="https://bosscreative.com/wp-content/themes/parada/videos/home/boss_home_video_1.mp4" type="video/mp4" />
      </video>
      <div className="video-text-overlay">
        <div className="video-text">
          let's make<br />your <div className="blue-text text-slider">
            <Slideshow slides={slides} />
          </div>
          <br />something to<br />show off
        </div>
      </div>
      <div className="scroll-down"><span></span></div>
    </div>
  );
};

export default FullscreenVideo;
